// Main Stylesheet

// Colors
$primary-color          : #d74600;
$secondary-color        : #ffcbcb;
$accent-color           : #6555ff;
$shade-color            : #789572;
$complementary-color    : #2f2f2f;

// Fonts
$body-font        : "Sofia Pro", sans-serif;
$heading-font     : "Sofia Pro", sans-serif;
$body-font-size   : 1.1rem;
$base-line-height : 1.8;


@import
  "_resets",
  "_utility",
  "_universal",
  "_mixins",
  "_buttons",
  "_nav",
  "_footer",
  "_header",
  "_media-queries";

.shaka-container {
  // position: relative;
  text-align: right;
  transform: translateY(-50%);

}
.shaka {
  height: 500px;
  left: 0 !important;
  position: relative !important;
}

.intro video {
  height  : 100%;
  width   : 100%;
  object-fit: contain;
}

.scramble {
  margin-top: -200px;
  p {
    font-weight: 200;
  }
  span {
    color: $accent-color;
    font-weight: 700;
  }
}

.reel {
  background-color: $accent-color;
  iframe {
    width: 800px;
    height: 600px;
  }
}
.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: left;
  // background: #fff;
  /* Center slide text vertically */
  // display: -webkit-box;
  // display: -ms-flexbox;
  // display: -webkit-flex;
  // display: flex;
  // -webkit-box-pack: center;
  // -ms-flex-pack: center;
  // -webkit-justify-content: center;
  // justify-content: center;
  // -webkit-box-align: center;
  // -ms-flex-align: center;
  // -webkit-align-items: center;
  // align-items: center;
  
  width: 800px;
  height: 600px;
  overflow: hidden;
  img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
  .portfolio-container {
    min-height: 450px;
    &.flex-center {
      flex-direction: column;
    }
  }
  .portfolio-title {
    font-size: 2.2rem;
    font-weight: bold;
    padding: 2rem 0;
    text-transform: uppercase;
  }
  .portfolio-subtitle {
    font-size: 1.5rem;
  }
  .portfolio-title,
  .portfolio-subtitle {
    display: block;
  }
  .portfolio-category {
    font-size: 1.2rem;
    padding: 0.6rem 1.2rem;
    border-radius: 5px;
    background-color: $accent-color;
    color: $secondary-color;
  }
  .clickable-image {
    border: 2px solid $accent-color;
  }
  .slide-frame {
    border: 2px solid $accent-color;
  }
  .portfolio-preview {
    width: 90%;
  }
}

.tickers p {
  font-size: 3.2rem;
  strong {
    color: $accent-color;
  }
}